import React from 'react';

class Controls extends React.Component {
    render() {
        if (this.props.page === 'ProductTable') {
            return (
                <div className="controls">
                    <button className="btn--block" onClick={() => {this.props.switchPage()}}>Корзина</button>
                </div>
            )
        } else {
            return (
                <div className="controls">
                    <button className="btn--block" onClick={() => {this.props.removeFromCart(0, 0, true)}}>Очистить корзину</button>
                    <button className="btn--block fl" onClick={() => {this.props.switchPage()}}>Список товаров</button>
                </div>
            )
        }

        
    }
}

export default Controls;