import React from 'react';

class Total extends React.Component {
    render() {
        if (this.props.page === 'ProductTable') {
            return (
                null
            )
        } else {

            let price = this.props.products.reduce((prev, item) => {
                for (let id in this.props.cart) {
                    if (item.id === parseInt(id)) {
                        console.log(prev);
                        prev += item.price * this.props.cart[id];
                        break;
                    }
                }
                return prev;
            }, 0);


            return (
                <div className="total">
                    <span className="total__text">Всего:</span>
                    <span className="total__price">${price}</span>
                </div>
            )
        }
        
    }
}

export default Total;