import React from 'react';
import ProductTable from './ProductTable';


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            page : 'ProductTable'
        }
    }


    render() {
        return (
            <main className="main">
                <ProductTable 
                page={this.state.page} 
                switchPage={this.switchPage}
                />
            </main>
        )
    }



    switchPage = () => {
        if (this.state.page === 'ProductTable') {
            this.setState({page: 'Cart'})
        } else {
            this.setState({page: 'ProductTable'})
        }
    }
}
export default App;