import React from 'react';
import TRow from './TRow';
class TBody extends React.Component {
    render() {
        let rows = [];
        if (this.props.page === 'ProductTable') {
            rows = this.props.products.map((item) => {
                return <TRow page={this.props.page} item={item} cart={this.props.cart} key={item.id} addToCart={this.props.addToCart}/>;
            })
        } else {
            for (let id in this.props.cart) {
                id = +id;
                let i = 0;
                while (this.props.products[i].id !== id) {
                    i += 1;
                }
                let row = <TRow page={this.props.page} item={this.props.products[i]} removeFromCart={this.props.removeFromCart} key={id} cart={this.props.cart}/>
                rows.push(row);
            }
        }
        
        return (
            <tbody className="tbody">
                {rows}
            </tbody>
        )
    }
}

export default TBody;