import React from 'react';

class THead extends React.Component {
    render() {
        let row;
        if (this.props.page === 'ProductTable') {
            row = (
                <>
                <td className="td thead__td">Название</td>
                <td className="td thead__td">Цена</td>
                <td className="td thead__td">Количество</td>
                <td className="td thead__td"></td>
                </>
            )
        } else {
            row = (
                <>
                <td className="td thead__td">Название</td>
                <td className="td thead__td">Цена</td>
                <td className="td thead__td">Количество</td>
                <td className="td thead__td"></td>
                <td className="td thead__td"></td>
                </>
            )
        }
        return (
            <thead className="thead">
                <tr>
                    {row}
                </tr>
            </thead>
        )
    }
}

export default THead;