import React from 'react';
import Header from './Header'
import Table from './Table';
import Controls from './Controls';
import products from '../products';

class ProductTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products : products(),
            cart : {}
        }
    }
    render() {
        return (
            <div className="page">
                <Header page={this.props.page}/>     
                <Table 
                page={this.props.page} 
                products={this.state.products} 
                addToCart={this.addToCart} 
                removeFromCart={this.removeFromCart}
                cart={this.state.cart}/>
                <Controls page={this.props.page} removeFromCart={this.removeFromCart} switchPage={this.props.switchPage}/>
            </div>
        )
    }

    addToCart = (id) => {
        this.setState((prevState) => {
            let i = 0;
            let products = prevState.products;
            while (products[i].id !== id) {
                i += 1;
            }

            if (products[i].count === 0) {
                alert('Товар закончился!');
                return;
            }

            if (products[i].count === prevState.cart[id]) {
                alert('Товар закончился!');
                return;
            }
            let cart = Object.assign({}, prevState.cart);
            if (cart[id]) {
                cart[id] += 1;
            } else {
                cart[id] = 1;
            }
            return {cart}
        })
    }

    removeFromCart = (id, count, all) => {
        this.setState((prevState) => {
            if (all) {
                return {cart: {}}
            }
            let i = 0;
            while (prevState.products[i].id !== id) {
                i += 1;
            }
            let cart = Object.assign({}, prevState.cart);
            cart[id] -= count;
            if (cart[id] === 0) {
                delete cart[id];
            }
            return {cart};
        })
        
    }
}

export default ProductTable;