import React from 'react';

class Header extends React.Component {
    render() {
        let content;
        if (this.props.page === 'ProductTable') {
            content = <h1 className="h1">Список товаров</h1>
        } else {
            content = <h1 className="h1">Корзина</h1>
        }
        return (
            <header className="header">
                {content}
            </header>
        )
    }
}

export default Header;