import React from 'react';

class TRow extends React.Component {
    render() {
        let row = [];
        if (this.props.page === 'ProductTable') {
            let count;
            if (this.props.cart[this.props.item.id]) {
                count = this.props.item.count - this.props.cart[this.props.item.id];
            } else {
                count = this.props.item.count;
            }
            
            row = (
                <>
                <td className="td tbody__td">{this.props.item.name}</td>
                <td className="td tbody__td">${this.props.item.price}</td>
                <td className="td tbody__td">{count}</td>
                <td className="td tbody__td">
                    <button className="btn btn--add" onClick={() => {this.props.addToCart(this.props.item.id)}}></button>
                </td>
                </>
            )
        } else {
            let count = parseInt(this.props.cart[this.props.item.id]);
            row = (
                <>
                <td className="td tbody__td">{this.props.item.name}</td>
                <td className="td tbody__td">${this.props.item.price}</td>
                <td className="td tbody__td">{count}</td>
                <td className="td tbody__td">
                    <button className="btn" onClick={() => {this.props.removeFromCart(this.props.item.id, 1, false)}}></button>
                </td>
                <td className="td tbody__td">
                    <button className="btn--block" onClick={() => {this.props.removeFromCart(this.props.item.id, count, false)}}>Удалить все</button>
                </td>
                </>
            )
        }
        return (
            <tr className="tbody__tr">
                {row}
            </tr>
        )
    }
}

export default TRow;